div.NavBar {
    position: fixed;
    z-index: 10;
    width: 100%;
    height: 94px;
    background-color: var(--header-bg);
    display: flex;
    justify-content: center;
    text-align: center;
    top:0;
}

div.NavList {
    display: flex;
    height: 100%;
    width: 100%;
    position: relative;
    padding-right: 35px;
}

img.NavIcon {
    max-height: 28px;
    max-width: 28px;
    transition: all 250ms;
}

ul.NavList, ul.NavIconList{
    display: flex;
    height: 100%;
    list-style: none;
    align-items: center;
    justify-content: flex-end;
}

ul.NavList {
    flex: 1;
    padding-right: 47px;
    font-family: 'Rubik';
    font-style: normal;
    font-size: 40px;
    line-height: 47px;
    letter-spacing: 0.15em;
}

ul.NavIconList {
    padding: 0px;
    gap: 14px;
    transform: scale(1);
}

/* img.NavIcon:hover {
    transform: scale(1.5);
} */

.NavLink {
    color:#fff;
    font-family: var(--rubik-mono), 'san-serif';
    font-size: 28px;
    letter-spacing: 0px;
    padding: 5px 30px;
    text-transform: uppercase;
    text-decoration: none;
    position: relative;
}

.NavLink::after {
    content: "";
    display: block;
    position: absolute;
    left: 30px;
    right:35px;
    height: 2px;
    background-color: #adc7ad;
    opacity: 0;
    transform-origin: center;
    transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
    transform: scaleX(0);
}

.NavLink:hover::after {
    cursor: pointer;
    transform: scaleX(1);
    opacity: 1;
}